@import '~santd/es/core/styles/themes/default.less';

@pro-global-footer-prefix-cls: ~'@{ant-prefix}-pro-global-footer';

.@{pro-global-footer-prefix-cls} {
    margin: 48px 0 24px 0;
    padding: 0 16px;
    text-align: center;

    &-links {
        margin-bottom: 8px;

        a {
            color: @text-color-secondary;
            transition: all .3s;

            &:not(:last-child) {
                margin-right: 40px;
            }

            &:hover {
                color: @text-color;
            }
        }
    }

    &-copyright {
        color: @text-color-secondary;
        font-size: @font-size-base;
    }
}

@primary-color: #1890ff;